import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Cookies from "js-cookie";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    beforeEnter: (to, from, next) => {
      if (Cookies.get("token") && Cookies.get("role") == 3000) {
        next("/appAdmin");
      } else if (Cookies.get("token") && Cookies.get("role") == 3001) {
        next("/appTrainer");
      } else if (Cookies.get("token") && Cookies.get("role") == 3002) {
        next("/appTrainer");
      } else {
        next();
      }
    }
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/login.vue"),
    beforeEnter: (to, from, next) => {
      if (Cookies.get("token") && Cookies.get("role") == 3000) {
        next("/appAdmin");
      } else if (Cookies.get("token") && Cookies.get("role") == 3001) {
        next("/appTrainer");
      } else if (Cookies.get("token") && Cookies.get("role") == 3002) {
        next("/appClient");
      } else {
        next();
      }
    }
  },
  {
    path: "/appAdmin",
    name: "Admin",
    component: () => import("../views/appAdmin.vue"),
    beforeEnter: (to, from, next) => {
      if (Cookies.get("token") && Cookies.get("role") == 3000) {
        next();
      } else {
        next("/login");
      }
    }
  },
  {
    path: "/appTrainer",
    name: "Trainer",
    component: () => import("../views/appTrainer.vue"),
    beforeEnter: (to, from, next) => {
      if (Cookies.get("token") && Cookies.get("role") == 3001) {
        next();
      } else {
        next("/login");
      }
    }
  },
  {
    path: "/appClient",
    name: "Client",
    component: () => import("../views/appClient.vue"),
    beforeEnter: (to, from, next) => {
      if (Cookies.get("token") && Cookies.get("role") == 3002) {
        next();
      } else {
        next("/login");
      }
    }
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
