const i18n = new VueI18n({
    locale: "en",
    fallbackLocale: 'en',

});

router.beforeEach(async (to, from, next) => {
    if (to.name == "Admin" && Cookies.get("token") || to.name == "Trainer" && Cookies.get("token") || to.name == "Client" && Cookies.get("token")) {
        await store.dispatch("getTranslations")
    }

    const translations = store.getters.translations

    Object.keys(translations).forEach(el => {
        i18n.setLocaleMessage(el, translations[el])
    })


    next()
})

export default i18n;
